.catalog {

	margin-bottom: rem(122);

	.container {
		display: flex;
		flex-direction: column;
	}

	.title {
		font-size: rem(36);
		font-weight: 600;
		color: var(--c-gray-title);
		margin-bottom: rem(65);
	}


	&__items {
		display: flex;
		flex-wrap: wrap;

		.block {
			margin-right: rem(146);
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(32);

		// svg {
		// 	margin-bottom: rem(8);

		// 	height: rem(28);
		// }

		.icon {
			margin-bottom: rem(8);
			height: rem(28);
			display: flex;
			align-items: flex-end;
		}

		.link {
			font-size: rem(20);
			font-weight: 600;
			color: #101010;
			margin-bottom: rem(32);
		}

		> ul {
			> li {
				margin-bottom: rem(18);

				> a {
					font-size: rem(16);
					color: rgba(62,62,62,.7);

					&:hover {
						color: var(--c-gray-title);
					}
				}

				ul {
					margin-top: rem(7);
					li {
						list-style: disc;
						margin-left: rem(20);
						color: rgba(62, 62, 62, .8);

						margin-bottom: rem(7);

						&:last-child {
							margin-bottom: 0;
						}

						a {
							font-size: rem(14);
							color: rgba(62, 62, 62, .8);

							&:hover {
								color: var(--c-gray-title);
							}
						}
					}
				}
			}
		}
	}

	&__filter {
		height: rem(58);
		background-color: white;
		border-radius: rem(5);
		padding: rem(0) rem(42) 0 rem(20);

		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: rem(20);

		&_item {
			display: flex;
			align-items: center;

			&:first-child {
				.left {
					color: #FFB571;
				}
				.right {
					color: black;
				}
			}

			button {
				display: flex;
				align-items: center;
			}

			.left {
				color: rgba(0,0,0, .56);
				margin-right: rem(12);
			}

			.right {
				color: rgba(92, 92, 92, .56);
				display: flex;
				align-items: center;

				svg {
					margin-left: rem(7);
				}
			}
		}
	}


	&__count {
		margin-bottom: rem(46);

		display: flex;
		align-items: center;

		span {
			margin-right: rem(20);
			color: var(--c-black-70);
			font-weight: 500;
		}

		&_btn {
			width: rem(30);
			height: rem(30);
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;

			color: var(--c-black-60);

			margin-right: rem(12);

			&:last-child {
				margin-right: 0;
			}
		}

		&_btn.active {
			background-color: black;
			color: white;
		}
	}

	&__product {
		display: flex;
		flex-wrap: wrap;

		.product_item {
			width: calc((100% - 44px) / 3);

			margin-right: rem(22);
			margin-bottom: rem(32);

			&:nth-child(3n) {
				margin-right: 0;
			}

			&__image {
				height: rem(280);
			}

			&__subtitle {
				font-size: rem(16);
			}

			&__list {
				ul {
					li {
						font-size: rem(14);
					}
				}
			}
		}
	}

}